<template>
<div id="app" class="ui text container">
  <div class="center aligned">
    <div class="column">
      <h2 class="h2 text-center">
        {{ msg }}
        <br>
        {{ duration }}
      </h2>
      <p>
        {{ hours }} :
        {{ minutes | zeroPad }} :
        {{ seconds | zeroPad }} :
        {{ milliSeconds | zeroPad(3) }}</p>
      <button class="btn btn-outline-primary btn-lg" @click="startTimer" v-if="!isRunning">START</button>
      <button class="btn btn-outline-primary btn-lg" @click="stopTimer" v-else>STOP</button>
      <button class="btn btn-outline-primary btn-lg" @click="pushTime" :disabled="!isRunning">LAP</button>
      <button class="btn btn-outline-primary btn-lg" @click="clearAll">CLEAR</button>
      <table class="table table-striped table-bordered menu-table" align=center>
        <tr style="table-tr" v-for="(mn, index) in menu" v-bind:key="index">
          <th class="th-pad1 menu-name">
            <input class="form-control menu-name" type="text" placeholder='Name' v-model=mn.name></th>
          <th class="th-pad1 menu-sec">
            <input class="form-control menu-sec" type="number" placeholder='integer seconds' v-model=mn.sec></th>
          <th class="th-pad1 menu-batu">
            <button class="btn btn-outline-primary btn-sm" @click="removeItem(index)">‚úó</button></th>
        </tr>
      </table>
      <button type="button" class="btn btn-outline-primary btn-sm"
        @click="addItem" data-toggle="tooltip" data-placement="top" title="Add Item">+</button>
      <button type="button" class="btn btn-outline-primary btn-sm"
        @click="saveMenu" data-toggle="tooltip" data-placement="top" title="Save items">&#x1F4BE;</button>
      <button type="button" class="btn btn-outline-primary btn-sm"
        @click="loadMenu" data-toggle="tooltip" data-placement="top" title="Load items">&#x1F4C2;</button>
      <button type="button" class="btn btn-outline-primary btn-sm"
        @click="loadDefaultMenu" data-toggle="tooltip" data-placement="top" title="Load default">&#x1F4C4;</button>
      <ul v-if="times.length">
        <li v-for="itm in times" v-bind:key="itm">
          {{ itm.hours  }} :
          {{ itm.minutes | zeroPad }} :
          {{ itm.seconds | zeroPad }} :
          {{ itm.milliSeconds | zeroPad(3) }}
        </li>
      </ul>
    </div>
    <!-- /.column -->
  </div>
  <!-- /.center aligned row -->
</div>
</template>

<style>
.menu-table {width: 24em; }
.menu-name {width: 16em; }
.menu-sec {width: 4em; }
.menu-batu {width: 4em; }
.btn-border-black {
  border-color: black
}
th.th-pad1 {
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 1px;
  padding-right: 1px;
}
</style>

<script>
import * as localforage from 'localforage'
export default {
  name: 'StopWatch',
  data () {
    return {
      times: [],
      animateFrame: 0,
      nowTime: 0,
      diffTime: 0,
      startTime: 0,
      isRunning: false,
      durationTime: 0,
      menu: []
    }
  },
  created: function () {
    this.loadMenu()
  },
  methods: {
    // ÁèæÂú®ÊôÇÂàª„Åã„ÇâÂºïÊï∞„Å´Ê∏°„Åó„ÅüÊï∞ÂÄ§„Çí startTime „Å´‰ª£ÂÖ•
    setSubtractStartTime: function (time) {
      var t = typeof time !== 'undefined' ? time : 0
      this.startTime = Math.floor(performance.now() - t)
    },
    // „Çø„Ç§„Éû„Éº„Çí„Çπ„Çø„Éº„Éà„Åï„Åõ„Çã
    startTimer: function () {
      // loop()ÂÜÖ„Åß this „ÅÆÂÄ§„ÅåÂ§âÊõ¥„Åï„Çå„Çã„ÅÆ„ÅßÈÄÄÈÅø
      var vm = this
      vm.setSubtractStartTime(vm.diffTime);
      // „É´„Éº„ÉóÂá¶ÁêÜ
      (function loop () {
        vm.nowTime = Math.floor(performance.now())
        vm.diffTime = vm.nowTime - vm.startTime
        vm.animateFrame = requestAnimationFrame(loop)
      }())
      vm.isRunning = true
    },
    // „Çø„Ç§„Éû„Éº„ÇíÂÅúÊ≠¢„Åï„Åõ„Çã
    stopTimer: function () {
      this.isRunning = false
      cancelAnimationFrame(this.animateFrame)
    },
    // Ë®àÊ∏¨‰∏≠„ÅÆÊôÇÈñì„ÇíÈÖçÂàó„Å´ËøΩÂä†
    pushTime: function () {
      this.times.push({
        hours: this.hours,
        minutes: this.minutes,
        seconds: this.seconds,
        milliSeconds: this.milliSeconds
      })
    },
    // ÂàùÊúüÂåñ
    clearAll: function () {
      this.startTime = 0
      this.nowTime = 0
      this.diffTime = 0
      this.times = []
      this.stopTimer()
      this.animateFrame = 0
      // this.durationTime = 0
    },
    addItem: function () {
      var itemName = 'N' + this.menu.length.toString()
      this.menu.push({name: itemName, sec: 10})
    },
    removeItem: function (idx) {
      this.menu.splice(idx, 1)
    },
    loadDefaultMenu: function () {
      this.menu = [
        {name: '„Éï„É´„Éó„É©„É≥„ÇØ', sec: 60},
        {name: '„Ç®„É´„Éú„Éº„Éó„É©„É≥„ÇØ', sec: 30},
        {name: 'ËÑö‰∏ä„Åí„Éó„É©„É≥„ÇØÂè≥', sec: 30},
        {name: 'ËÑö‰∏ä„Åí„Éó„É©„É≥„ÇØÂ∑¶', sec: 30},
        {name: '„É¨„Éï„Éà„Çµ„Ç§„Éâ„Éó„É©„É≥„ÇØ', sec: 30},
        {name: '„É©„Ç§„Éà„Çµ„Ç§„Éâ„Éó„É©„É≥„ÇØ', sec: 30},
        {name: '„Éï„É´„Éó„É©„É≥„ÇØ', sec: 30},
        {name: '„Ç®„É´„Éú„Éº„Éó„É©„É≥„ÇØ', sec: 60}
      ]
    },
    saveMenu: function () {
      const myLF = localforage.createInstance({
        drive: localforage.LOCALSTORAGE,
        name: 'MyLocal',
        storeName: 'pwa-plank-watch',
        version: 1
      })
      myLF.setItem('item', this.menu)
        .then(() => {
          // pass
        })
        .catch((error) => {
          console.log(error) // pass
        })
    },
    loadMenu: function () {
      const myLF = localforage.createInstance({
        drive: localforage.LOCALSTORAGE,
        name: 'MyLocal',
        storeName: 'pwa-plank-watch',
        version: 1
      })
      myLF.getItem('item')
        .then((value) => {
          if (value) {
            this.menu = value
          } else {
            this.loadDefaultMenu()
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  computed: {
    msg: function () {
      var tSec = Math.ceil(this.diffTime / 1000)
      var message = '„Åä„Å§„Åã„ÇåÔΩû'
      var menu = this.menu
      for (var i = 0; i < menu.length; i++) {
        var item = menu[i]
        if (tSec <= item.sec) {
          message = item.name
          this.durationTime = item.sec - tSec
          break
        } else {
          tSec -= item.sec
        }
      }
      return message
    },
    duration: function () {
      return this.durationTime
    },
    // ÊôÇÈñì„ÇíË®àÁÆó
    hours: function () {
      return Math.floor(this.diffTime / 1000 / 60 / 60)
    },
    // ÂàÜÊï∞„ÇíË®àÁÆó (60ÂàÜ„Å´„Å™„Å£„Åü„Çâ0ÂàÜ„Å´Êàª„Çã)
    minutes: function () {
      return Math.floor(this.diffTime / 1000 / 60) % 60
    },
    // ÁßíÊï∞„ÇíË®àÁÆó (60Áßí„Å´„Å™„Å£„Åü„Çâ0Áßí„Å´Êàª„Çã)
    seconds: function () {
      return Math.floor(this.diffTime / 1000) % 60
    },
    // „Éü„É™Êï∞„ÇíË®àÁÆó (1000„Éü„É™Áßí„Å´„Å™„Å£„Åü„Çâ0„Éü„É™Áßí„Å´Êàª„Çã)
    milliSeconds: function () {
      return Math.floor(this.diffTime % 1000)
    }
  },
  filters: {
    // „Çº„É≠Âüã„ÇÅ„Éï„Ç£„É´„Çø ÂºïÊï∞„Å´Ê°ÅÊï∞„ÇíÂÖ•Âäõ„Åô„Çã
    // ‚Äª String.prototype.padStart() „ÅØ IE„Åò„ÇÉ‰Ωø„Åà„Å™„ÅÑ
    zeroPad: function (value, num) {
      var n = typeof num !== 'undefined' ? num : 2
      return value.toString().padStart(n, '0')
    }
  }
}
</script>



